import { GreenButton } from 'feature/GreenButton';
import NewChallenge from 'feature/NewChallenge';
import { SearchBar } from 'feature/SearchBar';
import { useState, useEffect } from 'react';
import styled from 'styled-components';
import API from '../api/index';
import { Link, useNavigate } from 'react-router-dom';
declare global {
  interface Window {
    kakao: any;
  }
}
const StyledMapContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 100vh;
`;

const StyledMapItem = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
`;

const StyledNav = styled.nav`
  display: flex;
  align-items: center;

  height: 5.1rem;
`;

const StyledTitle = styled.h2`
  margin-right: auto;
`;

const StyledSubTitle = styled.h3`
  margin-right: auto;
  margin-bottom: 2rem;
`;

const StyledPadding = styled.div``;

const StyledPaddingBottom = styled.div`
  margin-bottom: 2rem;
`;

const StyledAddButton = styled.button`
  background-color: var(--green-100);
  color: var(--white);
  border-radius: 0.4rem;
  border: 1px solid rgba(217, 218, 218, 1);
  width: 7rem;
  height: 3rem;
`;

const StyledLink = styled(Link)`
  color: white;
  text-decoration: none;
`;
export const AddMap = () => {
  const [address, setAddress] = useState(''); //  
  const [placeName, setPlaceName] = useState(''); // 
  const [map, setMap] = useState(null); //  
  const [lat, setLat] = useState(0); //   
  const [longi, setLongi] = useState(0); //   
  const [PlaceId, setPlaceId] = useState(0);
  const accessToken = localStorage.getItem('accessToken');
  const handleChangeValue = (e: React.ChangeEvent<HTMLInputElement>) => {
    setAddress(e.target.value);
  };

  const PostMapData = async (lat: number, longi: number) => {
    try {
      const postData = {
        placeName: placeName, //  
        lat: lat, // 
        longi: longi, // 
      };
      console.log(' :', postData);
      const response = await API.POST({
        url: 'https://ok.greennare.store/nare/map',
        data: postData,
        headers: {
          Authorization: accessToken, //   .
          'Content-Type': 'application/json', //   content type .
        },
      });
      console.log('POST  ', response.data);
    } catch (err) {
      console.log('POST  ', err);
    }
  };
  interface MapData {
    placeId: number;
  }
  interface ID {
    id: number;
  }
  const DeleteMapData = async () => {
    try {
      const response = await API.GET({
        url: 'https://ok.greennare.store/nare/map',
      });
      if (response?.data?.length > 0) {
        //     placeId  
        const placeIds = response.data.map(
          (mapData: MapData) => mapData.placeId,
        );

        // placeIds     placeId .
        console.log('placeIds:', placeIds);

        // DeleteMapData   placeIds      
        placeIds.map(async (id: ID) => {
          try {
            const deleteResponse = await API.DELETE({
              url: `https://ok.greennare.store/nare/map/${id}`,
              headers: {
                Authorization: accessToken, //   .
                'Content-Type': 'application/json', //   content type .
              },
            });
            console.log(`DELETE   - ID: ${id}`, deleteResponse.data);
          } catch (error) {
            console.log(`DELETE   - ID: ${id}`, error);
          }
        });
      } else {
        console.log(' .');
      }
      console.log('GET  ', response);
    } catch (error) {
      console.log('GET  ', error);
    }
  };
  const handlechangeregister = () => {
    const geocoder = new window.kakao.maps.services.Geocoder();
    geocoder.addressSearch(address, function (result: any, status: any) {
      if (status === window.kakao.maps.services.Status.OK) {
        const coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);

        const marker = new window.kakao.maps.Marker({
          map: map,
          position: coords,
        });

        const infowindow = new window.kakao.maps.InfoWindow({
          content: `<div style="width:150px;text-align:center;padding:6px 0;">${placeName}</div>`,
        });
        infowindow.open(map, marker);

        map.setCenter(coords);
        console.log(':', coords.getLat());
        console.log(':', coords.getLng());
        setLat(coords.getLat()); // 
        setLongi(coords.getLng()); // 
        // post  
        PostMapData(coords.getLat(), coords.getLng()); // coords.getLat() coords.getLng()  
      }
    });
  };

  const handledeleteregister = () => {
    DeleteMapData();
  };
  useEffect(() => {
    const mapContainer = document.getElementById('map'); //   div
    const mapOption = {
      center: new window.kakao.maps.LatLng(33.450701, 126.570667), //  
      level: 3, //   
    };

    //  
    const map = new window.kakao.maps.Map(mapContainer, mapOption);
    setMap(map);
  }, []);

  return (
    <>
      <StyledNav>
        <StyledTitle>    </StyledTitle>
        <StyledAddButton>
          <StyledLink to={'/map'}></StyledLink>
        </StyledAddButton>
      </StyledNav>
      <StyledMapContainer>
        <StyledMapItem>
          <div id="map" style={{ width: '65%', height: '60%' }}></div>
        </StyledMapItem>
        <StyledMapItem>
          <StyledPadding>
            <StyledSubTitle>  </StyledSubTitle>{' '}
            <SearchBar onChange={handleChangeValue} value={address}></SearchBar>
            <StyledPaddingBottom />
            <NewChallenge setContents={setPlaceName} contents={placeName} />
          </StyledPadding>
          <div>
            <GreenButton onClick={handlechangeregister}></GreenButton>
            <GreenButton onClick={handledeleteregister}></GreenButton>
          </div>
        </StyledMapItem>
      </StyledMapContainer>
    </>
  );
};
